/* admin_estates_page_header */

#admin_estates_page_header{
	height: 13vh;
	width: 100%;
	position: sticky;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0,0.95);
	color: var(--fourth-color);
	z-index: 3;
}

#admin_estates_page_header_nav{
	height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-around;
}

#admin_estates_page_header_nav_a1{
	text-decoration: none;
	color: var(--fourth-color);
	padding: 1%;
}
#admin_estates_page_header_nav_a1:hover{
	background-color: var(--fourth-color);
	color: black;
}

#admin_estates_page_header_nav_h1{
	margin-right: 10%;
}

#admin_estates_page_header_nav_a2{
	text-decoration: none;
	color: var(--fourth-color);
	padding: 1%;
	padding-left: 2%;
	padding-right: 2%;
}
#admin_estates_page_header_nav_a2:hover{
	background-color: var(--fourth-color);
	color: black;
}


/* admin_estates_page_main */

#admin_estates_page_main{
	width: 100%;
	height: 110vh;
	background-color: var(--third-color);
	color: var(--fourth-color);
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow-y: scroll;
	padding-bottom: 5%;
}

/* admin_estates_page_main_section */

#admin_estates_page_main_section{

	width: 100%;
	height: auto;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: flex-start;
}

/* admin_estates_page_main_section_articles*/

.admin_estates_page_main_section_article{
	border: var(--fourth-color) solid 2px;
	border-radius: 5px;
	width: 80%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	height: 15vh;
	margin-left: 3%;
	position: relative;

	margin-top: 2%;
}

.admin_estates_page_main_section_article_div{
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	align-items: center;
	width: 100%;
	min-height: 100%;
	padding-left: 3%;
	box-sizing: border-box;


}

.admin_estates_page_main_section_article_div_h3{
	width: 75%;
	font-size: 150%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.admin_estates_page_main_section_article_div_span{
	width: 25%;
	height: 100%;
	border-left: var(--fourth-color) solid 2px;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	font-size: 120%;
}
.admin_estates_page_main_section_article_div_span:hover{
	background-color: var(--fourth-color);
	color: var(--third-color);
}

.admin_estates_page_main_section_article_div1{
	background-color: rgba(192, 200, 250);
	border: solid white 4px;
	border-radius: 10px;
	color: var(--third-color);
	width: 100%;
	height: auto;
	position: absolute;
	top: 105;
	left: 0;
	z-index: 2;
}

